import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Store } from '@ngxs/store';
import { UtilsService } from '../../@core/utils.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Constants } from '../../@global/constants';
import { OrganizationBasePage } from '../organization-base.page';
import { StorageService } from '../../@core/storage.service';
import { InfoPopoverComponent } from '../../@shared/components/info-popover/info-popover.component';
import { PopoverController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { FormCustomValidators } from '../../@shared/validators/form-custom-validators';
import { UpdateCurrentPageData, CreateOrganizationFlowData } from '../../@store/pages/pages.actions';
import { Camera } from '@ionic-native/camera/ngx';
import { InformService } from '../../@core/inform.service';

const PAGE_POSITION = 2;

@Component({
  selector: 'app-name-organization',
  templateUrl: './name-organization.page.html',
  styleUrls: ['./name-organization.page.scss'],
})
export class NameOrganizationPage extends OrganizationBasePage implements OnInit {
  public formGroup: FormGroup;
  public maxlength = Constants.MAX_LENGTH_TITLE;

  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    camera: Camera,
    informService: InformService,
    storage: StorageService,
    private formBuilder: FormBuilder,
    private translateService: TranslateService,
    private popoverController: PopoverController
  ) {
    super(location, router, store, utilsService, camera, informService, storage);
  }

  ngOnInit() {
    this.initForm();
  }

  private initForm(): void {
    this.formGroup = this.formBuilder.group({
      organizationName: ['', [Validators.required, Validators.maxLength(this.maxlength)]],
      tagline: ['', [Validators.maxLength(this.maxlength)]],
      web: ['', [Validators.maxLength(this.maxlength), FormCustomValidators.validUrl]],
    });
  }

  public async saveOrganizationData() {
    if (this.utilsService.isValidForm(this.formGroup)) {
      const data: CreateOrganizationFlowData = {
        complete_name: this.formGroup.get('organizationName').value,
        tagline: this.formGroup.get('tagline').value,
        website: this.formGroup.get('web').value,
      };

      this.store.dispatch(new UpdateCurrentPageData(data));

      this.goNextFlowPage(PAGE_POSITION);
    }
  }

  async presentPopover(event) {
    const text = this.translateService.instant(`name-organization.infoText`);
    const popover = await this.popoverController.create({
      event,
      component: InfoPopoverComponent,
      cssClass: 'custom-alert',
      translucent: true,
      componentProps: { text },
    });
    return await popover.present();
  }
}
